body {
  margin: 0;
}

h1{
  color: white;
  text-align: center;
  font-family: sans-serif;
}
h1{
  margin-top: 0px;
  background: skyblue;
  height: 50px;
  width: 100%;
}
h4{
  text-align: center;
  font-family: sans-serif;
}
/* Remove margins and padding from the list */
 p {
  margin: 0;
  padding: 0;
  display: inline-block;
}
/* Clear floats after the header */
.header:after {
  content: "";
  display: table;
  clear: both;
}

@media (max-width:800px) {
    #todos {
        width: 80%;
    }

    #newTask {
        max-width: 60%;
    }
}
.todo{
  border-bottom: gray 0.25px solid;
}
#sort {

  background-color: skyblue;
  color: white;
  border: none;
  bottom: 30%;
  right: 0%;
  width: 80px;
  font-size: 30px;
  float: bottom;
  margin-top: -10px;
  position: relative;
  float: left;
}
#sort:hover{
  background-color: navy;
}

/* Style the input */
input {
  width: 75%;
  padding: 10px;
  float: left;
  font-size: 30px;
  width: 90%;
  max-width: 75%;
}

.todo {
  cursor: pointer;
  position: relative;
  padding: 12px;
  list-style-type: none;
  font-size: 20px;

  /* make the list items unselectable */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


.todo:hover {
  background: skyblue;
}

/* When clicked on, add a background color and strike out text */
.completed  {
  background-color: lightgray;
}
.completed p {
    text-decoration: line-through;
    color: gray;
}



/* Style the close button */
.delete {
  text-align: center;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 40px;
  width: 40px;
  background-color: red;
  color: white;
}

.delete:hover {
  background-color: crimson;
}

.check {
  float: left;
   margin-right: 10px;
   width: 30px;
   height: 30px;
}

.check:hover {
  background-color: navy;
}

/* Style the input */
input {
  width: 90%;
  font-size: 30px;
  border: none;
  border-bottom: 1px gray solid;
  outline: none;
  max-width: 75%;
}


/* Style the "Add" button */
#add {
  position: relative;
  right: 10%;
  float: right;
  background-color: skyblue;
  color: white;
  border: none;
  width: 80px;
  font-size: 30px;
  margin-top: -10px;
}

#add:hover {
  background-color: navy;
}

